.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 82px;
    padding: 15px 0;
    margin-bottom: 40px;
    background-color: $gray;
    box-shadow: 0 0 30px 2px rgba(0,0,0,0.5);
    transition: height 0.4s ease;
    z-index: 10;

    &__burger {
        display: none;
        position: absolute;
        top: 32px;
        right: 20px;
        // transform: translateY(-50%)
        height: auto;
        width: 28px;

        background-color: transparent;

        @include r(768) {
            display: block;
            &--none {
                display: none; } }
        span {
            transition: 0.3s;
            display: block;
            height: 3px;
            width: 100%;

            background-color: $black;
            border-radius: 10px;

            &:not(:last-child) {
                margin-bottom: 4px; } }

        &.is-active {
            position: absolute;
            span {
                &:nth-child(1) {
                    transform: translateY(7px) rotate(45deg); }

                &:nth-child(2) {
                    transform: translateX(100%);
                    opacity: 0;
                    visibility: hidden; }

                &:nth-child(3) {
                    transform: translateY(-7px) rotate(-45deg); } } } }
    @include r(768) {
        padding: 10px 0; }

    &.is-active {
        height: 375px;
        overflow: hidden;
        // .header__burger
 } }        //     top: 30px

