.main-nav {
	display: flex;
	justify-content: space-between;
	-webkit-align-items: center;
	align-items: center;


	&__list {
		transition: all 0.4s ease;
		display: flex;
		@include r(768) {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: scale(0) translate(-50%, -50%);
			opacity: 0;
			margin: 40px auto;
			flex-direction: column;
			align-items: center; } }

	&__item {
		margin-right: 20px;
		&:last-child {
			margin-right: 0; }
		a {
			position: relative;
			display: block;
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 4px;
			text-transform: uppercase;
			opacity: 0.8;
			transition: all 0.4s ease;
			&:after {

				position: absolute;
				bottom: -5px;
				left: 0;
				content: '';
				height: 2px;
				width: 0%;
				background-color: $black;
				transition: width 0.3s ease; } }
		&:hover	a, &.is-active a {
			opacity: 1;
			&:after {
				width: 100%; } } }
	@include r(768) {
		&__item {
			margin-right: 0;
			margin-bottom: 20px;
			a {
				font-size: 30px; } } }

	&.is-active {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		.main-nav__list {
			@include r(768) {
				opacity: 1;
				transform: scale(1) translate(-50%, -50%); } } } }


