$black: #505050;
$gray: #eeeeee;

@include font('Reforma bold', 'reforma-grotesk-bold-c');
@include font('Istok web regular', 'istok-web-regular');
@include font('TT Commons medium', 'ttcommons-medium-webfont');

@mixin ref-bold {
    font-family: 'Reforma bold', sans-serif;
    font-weight: normal; }

@mixin istok {
    font-family: 'Istok web regular', sans-serif;
    font-weight: normal; }

@mixin com {
    font-family: 'TT Commons medium', sans-serif;
    font-weight: normal; }
