body {
	@include ref-bold;
	line-height: 1.4;
	color: $black;
	margin: 0;
	padding: 0; }

a {
	text-decoration: none;
	color: $black; }

.main-title {
	margin-bottom: 20px;
	font-size: 24px;
	text-align: center;
	text-transform: uppercase;
	@include r(768) {
		font-size: 18px;
		margin-bottom: 15px; } }

main {
	padding-top: 120px;
	@include r(768) {
		padding-top: 100px; } }
.container {
	width: 1200px;
	margin: 0 auto;
	@include r(1240) {
		width: 96%;
		padding: 0 20px; } }

.logo {
	letter-spacing: 3px;
	h1 {
		font-size: 30px;
		line-height: 1;
		white-space: nowrap;
		text-transform: capitalize; }
	p {
		display: flex;
		flex-direction: column;
		span {
			font-size: 12px;
			line-height: 1.01;
			opacity: 0.8; } } }

.sticky-wrapper {
	position: absolute;
	left: 0;
	right: 0;
	z-index: 10; }

#page-preloader {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background: #ffffff;
	z-index: 100; }

#page-preloader .spinner {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background: url('../img/Dasha.jpg') no-repeat 50% 50%;
	background-size: cover; }
