.gallery {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	padding-bottom: 30px;

	&__item {
		display: flex;
		width: 24%;
		box-shadow: 0 0 20px 1px rgba(0,0,0,0.6);
		@include css-lock(150, 200, 1024, 1240, height, px);
		margin-bottom: 20px;
		&:nth-child(-n+3) {
			width: 32%; }
		&:nth-child(4), &:nth-child(5), &:nth-child(10), &:nth-child(11) {
			width: 49%;
			@include css-lock(200, 300, 1024, 1240, height, px); }
		&:last-child {
			margin-bottom: 0; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; }
		&:hover {
			transform: scale(1.05);
			box-shadow: 0 0 40px 1px rgba(0,0,0,0.8); } }

	@include r(768) {

		&__item {
			&, &:nth-child(-n+3), &:nth-child(4), &:nth-child(5),&:nth-child(10), &:nth-child(11) {
				width: 100%;
				@include css-lock(150, 300, 320, 768, height, px); } } } }
