html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-dasha {
  width: 1.53em;
  height: 1em;
  fill: none;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "Reforma bold";
  src: url("../fonts/reforma-grotesk-bold-c.woff2") format("woff2"), url("../fonts/reforma-grotesk-bold-c.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Istok web regular";
  src: url("../fonts/istok-web-regular.woff2") format("woff2"), url("../fonts/istok-web-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "TT Commons medium";
  src: url("../fonts/ttcommons-medium-webfont.woff2") format("woff2"), url("../fonts/ttcommons-medium-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Reforma bold', sans-serif;
  font-weight: normal;
  line-height: 1.4;
  color: #505050;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #505050;
}

.main-title {
  margin-bottom: 20px;
  font-size: 24px;
  text-align: center;
  text-transform: uppercase;
}

@media only screen and (max-width: 768px) {
  .main-title {
    font-size: 18px;
    margin-bottom: 15px;
  }
}

main {
  padding-top: 120px;
}

@media only screen and (max-width: 768px) {
  main {
    padding-top: 100px;
  }
}

.container {
  width: 1200px;
  margin: 0 auto;
}

@media only screen and (max-width: 1240px) {
  .container {
    width: 96%;
    padding: 0 20px;
  }
}

.logo {
  letter-spacing: 3px;
}

.logo h1 {
  font-size: 30px;
  line-height: 1;
  white-space: nowrap;
  text-transform: capitalize;
}

.logo p {
  display: flex;
  flex-direction: column;
}

.logo p span {
  font-size: 12px;
  line-height: 1.01;
  opacity: 0.8;
}

.sticky-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 10;
}

#page-preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #ffffff;
  z-index: 100;
}

#page-preloader .spinner {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: url("../img/Dasha.jpg") no-repeat 50% 50%;
  background-size: cover;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 82px;
  padding: 15px 0;
  margin-bottom: 40px;
  background-color: #eeeeee;
  box-shadow: 0 0 30px 2px rgba(0, 0, 0, 0.5);
  transition: height 0.4s ease;
  z-index: 10;
}

.header__burger {
  display: none;
  position: absolute;
  top: 32px;
  right: 20px;
  height: auto;
  width: 28px;
  background-color: transparent;
}

@media only screen and (max-width: 768px) {
  .header__burger {
    display: block;
  }
  .header__burger--none {
    display: none;
  }
}

.header__burger span {
  transition: 0.3s;
  display: block;
  height: 3px;
  width: 100%;
  background-color: #505050;
  border-radius: 10px;
}

.header__burger span:not(:last-child) {
  margin-bottom: 4px;
}

.header__burger.is-active {
  position: absolute;
}

.header__burger.is-active span:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}

.header__burger.is-active span:nth-child(2) {
  transform: translateX(100%);
  opacity: 0;
  visibility: hidden;
}

.header__burger.is-active span:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

@media only screen and (max-width: 768px) {
  .header {
    padding: 10px 0;
  }
}

.header.is-active {
  height: 375px;
  overflow: hidden;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center;
}

.main-nav__list {
  transition: all 0.4s ease;
  display: flex;
}

@media only screen and (max-width: 768px) {
  .main-nav__list {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: scale(0) translate(-50%, -50%);
    opacity: 0;
    margin: 40px auto;
    flex-direction: column;
    align-items: center;
  }
}

.main-nav__item {
  margin-right: 20px;
}

.main-nav__item:last-child {
  margin-right: 0;
}

.main-nav__item a {
  position: relative;
  display: block;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 4px;
  text-transform: uppercase;
  opacity: 0.8;
  transition: all 0.4s ease;
}

.main-nav__item a:after {
  position: absolute;
  bottom: -5px;
  left: 0;
  content: '';
  height: 2px;
  width: 0%;
  background-color: #505050;
  transition: width 0.3s ease;
}

.main-nav__item:hover a, .main-nav__item.is-active a {
  opacity: 1;
}

.main-nav__item:hover a:after, .main-nav__item.is-active a:after {
  width: 100%;
}

@media only screen and (max-width: 768px) {
  .main-nav__item {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .main-nav__item a {
    font-size: 30px;
  }
}

.main-nav.is-active {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media only screen and (max-width: 768px) {
  .main-nav.is-active .main-nav__list {
    opacity: 1;
    transform: scale(1) translate(-50%, -50%);
  }
}

.gallery {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 30px;
}

.gallery__item {
  display: flex;
  width: 24%;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.6);
  height: 200px;
  height: calc(150px + (200 - 150) * ((100vw - 1024px) / (1240 - 1024)));
  margin-bottom: 20px;
}

@media (max-width: 1024px) {
  .gallery__item {
    height: 150px;
  }
}

@media (min-width: 1240px) {
  .gallery__item {
    height: 200px;
  }
}

.gallery__item:nth-child(-n+3) {
  width: 32%;
}

.gallery__item:nth-child(4), .gallery__item:nth-child(5), .gallery__item:nth-child(10), .gallery__item:nth-child(11) {
  width: 49%;
  height: 300px;
  height: calc(200px + (300 - 200) * ((100vw - 1024px) / (1240 - 1024)));
}

@media (max-width: 1024px) {
  .gallery__item:nth-child(4), .gallery__item:nth-child(5), .gallery__item:nth-child(10), .gallery__item:nth-child(11) {
    height: 200px;
  }
}

@media (min-width: 1240px) {
  .gallery__item:nth-child(4), .gallery__item:nth-child(5), .gallery__item:nth-child(10), .gallery__item:nth-child(11) {
    height: 300px;
  }
}

.gallery__item:last-child {
  margin-bottom: 0;
}

.gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery__item:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px 1px rgba(0, 0, 0, 0.8);
}

@media only screen and (max-width: 768px) {
  .gallery__item, .gallery__item:nth-child(-n+3), .gallery__item:nth-child(4), .gallery__item:nth-child(5), .gallery__item:nth-child(10), .gallery__item:nth-child(11) {
    width: 100%;
    height: 300px;
    height: calc(150px + (300 - 150) * ((100vw - 320px) / (768 - 320)));
  }
}

@media only screen and (max-width: 768px) and (max-width: 320px) {
  .gallery__item, .gallery__item:nth-child(-n+3), .gallery__item:nth-child(4), .gallery__item:nth-child(5), .gallery__item:nth-child(10), .gallery__item:nth-child(11) {
    height: 150px;
  }
}

@media only screen and (max-width: 768px) and (min-width: 768px) {
  .gallery__item, .gallery__item:nth-child(-n+3), .gallery__item:nth-child(4), .gallery__item:nth-child(5), .gallery__item:nth-child(10), .gallery__item:nth-child(11) {
    height: 300px;
  }
}

.videos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.videos__item {
  width: 100%;
  max-width: 32%;
  height: 250px;
  height: calc(200px + (250 - 200) * ((100vw - 1024px) / (1240 - 1024)));
  margin-bottom: 20px;
}

@media (max-width: 1024px) {
  .videos__item {
    height: 200px;
  }
}

@media (min-width: 1240px) {
  .videos__item {
    height: 250px;
  }
}

@media only screen and (max-width: 900px) {
  .videos__item {
    min-width: 48%;
    max-width: 48%;
  }
}

@media only screen and (max-width: 600px) {
  .videos__item {
    min-width: 100%;
    max-width: 100%;
  }
}

.video {
  position: relative;
  height: 100%;
  width: 100%;
  font-family: 'TT Commons medium', sans-serif;
  font-weight: normal;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.7);
  transition: all 0.3s ease;
}

.video__media {
  position: relative;
  width: 100%;
  height: calc(100% - 55px);
  background-size: cover;
  background-position: center center;
  transition: all 0.3s ease;
}

.video__media.is-active {
  height: 100%;
}

.video__play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  font-size: 0;
  cursor: pointer;
}

.video__play img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 35px;
}

.video__play:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: '';
  height: 0;
  width: 0;
  height: 30px;
  width: 30px;
  background-color: #FFF;
  z-index: -1;
}

.video__play.hide {
  display: none;
}

.video__title {
  display: flex;
  justify-content: space-between;
  height: 55px;
  font-family: 'Reforma bold', sans-serif;
  font-weight: normal;
  color: black;
  font-size: 18px;
  letter-spacing: 1px;
  padding: 15px;
}

.video__title + p {
  padding-top: 0;
}

.video__title span {
  color: gray;
  font-size: 14px;
}

.video__title.hide {
  display: none;
}

.video:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px 1px rgba(0, 0, 0, 0.8);
}

.contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contacts__wrap {
  margin-left: 100px;
  display: flex;
  flex-direction: column;
}

.contacts__name {
  font-size: 40px;
}

.contacts__prof {
  line-height: 1.05;
  color: gray;
  letter-spacing: 2px;
}

.contacts__prof:last-of-type {
  margin-bottom: 30px;
}

.contacts__cont {
  font-size: 20px;
  line-height: 2;
  letter-spacing: 1px;
  padding-left: 40px;
  background-image: url("../img/phone.svg");
  background-size: 30px;
  background-position: top 5px left;
  background-repeat: no-repeat;
}

.contacts__cont:last-of-type {
  background-image: url("../img/mail.svg");
}

.contacts__image {
  font-size: 0;
  margin-right: 100px;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.7);
}

@media only screen and (max-width: 768px) {
  .contacts {
    flex-direction: column;
  }
  .contacts__image, .contacts__wrap {
    margin: 0 0 30px;
  }
}
