.videos {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	&__item {
		width: 100%;
		max-width: 32%;
		@include css-lock(200, 250, 1024, 1240, height, px);
		margin-bottom: 20px; }

	@include r(900) {
		&__item {
			min-width: 48%;
			max-width: 48%; } }
	@include r(600) {
		&__item {
			min-width: 100%;
			max-width: 100%; } } }

.video {
	$this: &;
	position: relative;
	height: 100%;
	width: 100%;
	@include com;
	box-shadow: 0 0 20px 1px rgba(0,0,0,0.7);
	transition: all 0.3s ease;

	&__media {
		position: relative;
		width: 100%;
		height: calc(100% - 55px);
		background-size: cover;
		background-position: center center;
		transition: all 0.3s ease;
		&.is-active {
			height: 100%; } }
	&__play {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		height: 100%;
		font-size: 0;
		cursor: pointer;
		img {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 50px;
			height: 35px; }

		&:before {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			content: '';
			height: 0;
			width: 0;
			height: 30px;
			width: 30px;
			background-color: #FFF;
			z-index: -1; }
		&.hide {
			display: none; } }

	&__title {
		display: flex;
		justify-content: space-between;
		height: 55px;
		@include ref-bold;
		color: black;
		font-size: 18px;
		letter-spacing: 1px;
		padding: 15px;
		& + p {
			padding-top: 0; }
		span {
			color: gray;
			font-size: 14px; }
		&.hide {
			display: none; } }

	&:hover {
		transform: scale(1.05);
		box-shadow: 0 0 40px 1px rgba(0,0,0,0.8); } }
