.contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__wrap {
        margin-left: 100px;
        display: flex;
        flex-direction: column; }

    &__name {
        font-size: 40px; }

    &__prof {
        line-height: 1.05;
        color: gray;
        letter-spacing: 2px;
        &:last-of-type {
            margin-bottom: 30px; } }

    &__cont {
        font-size: 20px;
        line-height: 2;
        letter-spacing: 1px;
        padding-left: 40px;
        background-image: url('../img/phone.svg');
        background-size:  30px;
        background-position: top 5px left;
        background-repeat: no-repeat;
        &:last-of-type {
            background-image: url('../img/mail.svg'); } }

    &__image {
        font-size: 0;
        margin-right: 100px;
        box-shadow: 0 0 15px 1px rgba(0,0,0,0.7); }

    @include r(768) {
        flex-direction: column;
        &__image, &__wrap {
            margin: 0 0 30px; } } }
